import React, { useState } from 'react';
import { AppBar, Box, Toolbar, Typography, Button, IconButton, Drawer, List, ListItem, Hidden } from '@mui/material';
import { Link } from 'react-router-dom';
import MenuIcon from '@mui/icons-material/Menu';
import Brightness4Icon from '@mui/icons-material/Brightness4'; //    
import Brightness7Icon from '@mui/icons-material/Brightness7'; //    

const Header = ({ darkMode, setDarkMode }) => {
    const [drawerOpen, setDrawerOpen] = useState(false);
    const handleDarkMode = () => {
        setDarkMode(!darkMode);
    };

    const toggleDrawer = (open) => (event) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }
        setDrawerOpen(open);
    };

    const list = () => (
        <Box
            sx={{ width: 250 }}
            role="presentation"
            onClick={toggleDrawer(false)}
            onKeyDown={toggleDrawer(false)}
        >
            <List>
                <ListItem button component={Link} to="/register"></ListItem>
                <ListItem button component={Link} to="/login"></ListItem>
                <ListItem button component={Link} to="/students"> </ListItem>
            </List>
        </Box>
    );

    return (
        <AppBar position="static">
            <Toolbar>
                <Hidden mdUp>
                    <IconButton edge="start" color="inherit" aria-label="menu" onClick={toggleDrawer(true)}>
                        <MenuIcon />
                    </IconButton>
                    <Drawer anchor="left" open={drawerOpen} onClose={toggleDrawer(false)}>
                        {list()}
                    </Drawer>
                </Hidden>
                <Typography variant="h6" sx={{ flexGrow: 1 }}>  </Typography>
                <Hidden smDown>
                    <Button component={Link} to="/register" color="inherit"></Button>
                    <Button component={Link} to="/login" color="inherit"></Button>
                    <Button component={Link} to="/students" color="inherit"> </Button>
                </Hidden>
                <IconButton onClick={handleDarkMode} color="inherit">
                    {darkMode ? <Brightness7Icon /> : <Brightness4Icon />}
                </IconButton>
            </Toolbar>
        </AppBar>
    );
};

export default Header;
