import React, { useEffect, useState } from 'react';
import { Container, Table, TableBody, TableCell, TableHead, TableRow, Box, Typography, Hidden } from '@mui/material';

const StudyDatesRenderer = (params) => {
    const studyDates = params; //    study_dates
    const formattedDates = studyDates.map((dateString) => {
        const date = new Date(dateString);
        const day = date.getDate().toString().padStart(2, '0');
        const month = (date.getMonth() + 1).toString().padStart(2, '0');
        const year = date.getFullYear().toString();
        return `${day}.${month}.${year}`;
    }).join('; '); //      

    return <div>{formattedDates}</div>; //   
};

const Students = () => {
    const [students, setStudents] = useState([]);

    useEffect(() => {
        const fetchStudents = async () => {
            try {
                const response = await fetch('http://localhost:5000/api/students');
                const data = await response.json();
                console.log(data)
                setStudents(data);
            } catch (error) {
                console.error(error);
            }
        };
        fetchStudents();
    }, []);

    return (
        <>
            <Container maxWidth="md">
                <Hidden smDown>
                    <Table sx={{ marginTop: 4 }}>
                        <TableHead>
                            <TableRow>
                                <TableCell>'</TableCell>
                                <TableCell></TableCell>
                                <TableCell> </TableCell>
                                <TableCell> </TableCell>
                                <TableCell> </TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {students.map((student) => (
                                <TableRow key={student._id}>
                                    <TableCell>{student.first_name}</TableCell>
                                    <TableCell>{student.last_name}</TableCell>
                                    <TableCell>{StudyDatesRenderer(student.study_dates)}</TableCell>
                                    <TableCell>#todo</TableCell>
                                    <TableCell>{student.email}</TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </Hidden>
                <Hidden mdUp>
                    {students.map((student) => (
                        <Box key={student._id} sx={{ margin: 2, padding: 2, border: '1px solid grey', borderRadius: 2 }}>
                            <Typography variant="h6">{student.first_name} {student.last_name}</Typography>
                            <Typography variant="body1"> : {StudyDatesRenderer(student.study_dates)}</Typography>
                            <Typography variant="body1"> : #todo</Typography>
                            <Typography variant="body1"> : {student.email}</Typography>
                        </Box>
                    ))}
                </Hidden>
            </Container>
        </>
    );

};

export default Students;